import React, { Component } from 'react';
import ReactCompareImage from 'react-compare-image';
import { CubeGrid } from 'better-react-spinkit';

export class Device extends Component {
    constructor(props) {
        super(props);
        this.state = {
            img: '',
            pictureTaken: '',
            imageArray: [],
            baseImg: '',
            compareImg: '',
            sameImage: true,
            imgRetreived: '',
            baseImgUrl: '',
            compImgUrl: ''
        }

        this.takePicture = this.takePicture.bind(this);
        this.getImage = this.getImage.bind(this);
        this.compareImages = this.compareImages.bind(this);
        this.getTwilioImg = this.getTwilioImg.bind(this);
    }

    // Take picutre with device
    takePicture() {
        let url = `https://tcksz3lkhc.execute-api.us-east-1.amazonaws.com/dev/takePicture`;

        console.log("taking pic");
        return (window.fetch(url, {
            method: "GET",
        }).then((response) => {
            return response.json();
        }).then((response) => {
            console.log(response.sid);
            this.setState({
                pictureTaken: response.message
            });
        }).catch((err) => {
            console.log(err);
        }));
    }

    getTwilioImg() {
        let url = `https://tcksz3lkhc.execute-api.us-east-1.amazonaws.com/dev/getTwilioImg`;

        return (window.fetch(url, {
            method: "GET",
        }).then((response) => {
            return response.json();
        }).then((response) => {
            this.setState({ imgRetreived: response.message });
            window.location.reload();
        }).catch((error) => {
            console.log(error);
            this.setState({ imgRetreived: "There was an error getting the image from Twilio." })
        }));
    }

    // Get image from AWS S3 and display it
    getImage(img1, img2) {
        // let url = `https://tcksz3lkhc.execute-api.us-east-1.amazonaws.com/dev/img?imgName=${imageName}`;

        // return (window.fetch(url, {
        //     method: "GET",
        // }).then((response) => {
        //     return response.json();
        // }).then((response) => {
        //     console.log(response.imgURL);
        //     this.setState({
        //         img: response.imgURL
        //     });
        // }).catch((err) => {
        //     console.log(err);
        // }));

        const urls = [
            `https://tcksz3lkhc.execute-api.us-east-1.amazonaws.com/dev/img?imgName=${img1}`,
            `https://tcksz3lkhc.execute-api.us-east-1.amazonaws.com/dev/img?imgName=${img2}`
        ];

        Promise.all(urls.map(url =>
            fetch(url)
                .then(res => res.json())
        )).then(data => {
            this.setState({
                baseImgUrl: data[0].imgURL,
                compImgUrl: data[1].imgURL
            })
        }).catch((err) => {
            console.log("Error getting images from S3: ", err);
        });
    }

    compareImages(comp) {
        if (this.state.baseImg === comp && this.state.baseImg !== null) {
            this.setState({ sameImage: true })
        } else {
            this.setState({ sameImage: false })
        }
    }

    // Get all image names from S3
    componentDidMount() {
        let url = "https://tcksz3lkhc.execute-api.us-east-1.amazonaws.com/dev/listImgs";

        return (window.fetch(url, {
            method: "GET",
        }).then((response) => {
            return response.json();
        }).then((response) => {
            console.log(response);
            this.setState({ imageArray: response.imgs });
        }).catch((err) => {
            console.log(err);
        }));
    }

    render() {
        return (
            <div className="w-100 h-100 m-0 row">

                <div className="col-4 p-0 right-border text-center">

                    <div className="w-100 h-10 bg-dark">
                        <h2 className="text-light pt-2 text-center">Device Tools</h2>
                    </div>

                    <div className="card no-round">
                        <div className="card-body">
                            <h4 className="card-title">The device tools allow you to:</h4>
                            <ul className="ul">
                                <li>Take a picture from the device </li>
                                <li>Search through stored images</li>
                                <li>Get information about the device</li>
                            </ul>
                        </div>
                    </div>

                    <div className="card no-round">
                        <div className="card-body">
                            {
                                this.state.pictureTaken
                                    ? <div className="col-10">
                                        <p>{this.state.pictureTaken}</p>
                                    </div>
                                    : <p className="col-10 mt-2 ml-4">Take a new picture from the device.</p>
                            }
                            <button className="btn btn-dark btn-md text-pink" onClick={this.takePicture}>Take picture</button>
                            <button className="ml-3 btn btn-dark btn-md text-pink" onClick={this.getTwilioImg}>Get image</button>
                            {
                                this.state.imgRetreived
                                    ? <div className="mt-3">{this.state.imgRetreived}</div>
                                    : <div></div>
                            }
                        </div>
                    </div>

                    <div className="card no-round">
                        <div className="card-body">
                            <p className="col-10 mt-2 ml-4">Pick the base image to compare powerline obstruction with: </p>

                            <select className="form-control-md" id="exampleFormControlSelect1" onChange={(e) => {
                                e.preventDefault();
                                this.setState({ baseImg: e.target.value });
                            }} >
                                <option hidden disabled selected value> - Select base image - </option>
                                {
                                    this.state.imageArray.map((val, index) =>
                                        <option key={index}>
                                            {val}
                                        </option>)
                                }
                            </select>

                        </div>
                    </div>

                    <div className="card no-round">
                        <div className="card-body">
                            <p className="col-10 mt-2 ml-4">Find a picture that was taken by the device to compare with the defualt image of the powerline (the base image): </p>

                            <select className="selectpicker" data-style="btn-dark" title="Select an image" onChange={(e) => {
                                e.preventDefault();
                                this.setState({ compareImg: e.target.value });
                                this.getImage(this.state.baseImg, e.target.value);
                                this.compareImages(e.target.value);
                            }} >
                                <option hidden disabled selected value>Select image to compare...</option>
                                {
                                    this.state.imageArray.map((val, index) =>
                                        <option key={index}>
                                            {val}
                                        </option>)
                                }
                            </select>

                        </div>
                    </div>

                </div>

                <div className="col-8 p-0 text-center">
                    <h2 className="mt-2">Compare Images</h2>
                    {
                        this.state.baseImg === "Base image" && (this.state.compareImg && this.state.compareImg !== "Base image")
                            ? <div className="maxWidth">
                                <div className="vegeImage">
                                    <ReactCompareImage
                                        leftImageLabel="Base"
                                        rightImageLabel="Comparison"
                                        sliderLineColor="#E20074"
                                        leftImage="https://s3.amazonaws.com/tmo-img-func-dev-serverlessdeploymentbucket-lv2gr6nloke1/Base+image"
                                        rightImage="https://s3.amazonaws.com/tmo-img-func-dev-serverlessdeploymentbucket-lv2gr6nloke1/Ying's+Wio+board+5-30-2019-1" />
                                </div>
                                {
                                    !this.state.sameImage
                                        ? <div className="mt-5">
                                            <h4 className="mt-custom"><b>There is an obstruction</b> on the powerline!</h4>
                                            <p>Difference between images: <b>23.45%</b></p>

                                            <p className="mt-5">Edge detection for both images:</p>
                                            <div className="row m-0">
                                                <img className="col-6 edgeImage" src={"img/base.png"} alt="base edge detection" />
                                                <img className="col-6 edgeImage" src={"img/comp.png"} alt="comp edge detection" />
                                            </div>

                                            <p className="mt-5">Difference between images (outlined in RED)</p>
                                            <img className="diffImage mb-5" src={"img/diff.png"} alt="comparison output" />
                                        </div>
                                        : <div className="center-spinner">
                                            <CubeGrid size={100} color='#E20074' />
                                        </div>
                                }
                            </div>
                            : <div className="card bg-primary img-margin">
                                <div className="card-body">
                                    <h5>Please select an image taken by the device!</h5>
                                </div>
                            </div>
                    }

                </div>

            </div>
        )
    }
}